<template>
  <el-menu mode="horizontal" class="nav-menu"
           background-color="#1F2937"
           text-color="#FFFFFF"
           active-text-color="#ffd04b">
    <div class="container" style="height:61px;line-height: 61px">
      <img alt="icp-logo" class="nav-icon" src="@/assets/images/logo@2x.png"/>
      <el-tooltip class="item" effect="dark" content="暂时还不能使用ICP登录" placement="bottom">
        <el-button class="login" type="danger" round>{{$t('button.login')}}</el-button>
      </el-tooltip>
      <!--item从右到左-->
     <el-menu-item  @click="changeLang()" index="0" class="item-right">
       <!--<el-select v-model="value" placeholder="请选择">-->
         <!--<el-option-->
           <!--v-for="item in language"-->
           <!--:key="item.value"-->
           <!--:label="item.label"-->
           <!--:value="item.value">-->
         <!--</el-option>-->
       <!--</el-select>-->
       {{$t('button.language')}}
     </el-menu-item>
      <a href="https://dfinity.org/grants/"><el-menu-item index="1" class="item-right">{{$t('button.grant')}}</el-menu-item></a>
      <a href="https://www.icpleague.com/"><el-menu-item index="2" class="item-right">{{$t('button.forum')}}</el-menu-item></a>
      <!--<el-menu-item index="3" class="item-right">黑客松</el-menu-item>-->
      <el-menu-item index="4" class="item-right">{{$t('button.dapps')}}</el-menu-item>
      <a href="https://shuzhichengspace.gitbook.io/dfinity/yi-kuai-su-ru-men"><el-menu-item index="5" class="item-right">
        {{$t('button.library')}}</el-menu-item></a>
    </div>
  </el-menu>
</template>

<script>
    export default {
      name: "league-nav",
      data() {
        return {
          language: [{
            value: 'cn',
            label: '中文'
          }, {
            value: 'en',
            label: 'English'
          }],
          value: 'cn'
        }
      },
      watch:{
          value(val,oldVal){
            this.switchLang(val);
          }
      },
      methods:{
        //切换语言
        switchLang(lang)  {
          this.$i18n.locale = lang
        },
        changeLang(){
          const lang =this.$i18n.locale;
          if(lang==="cn")
            this.$i18n.locale="en";
          else if(lang==="en")
            this.$i18n.locale="cn";
        }
      }
    }
</script>
<style>
  .el-input__inner{
    background-color: #111827!important;
    border: 1px solid #32353c!important;;
    color: #fcfdff!important;
  }
</style>
<style scoped>
  @import "../assets/css/container.css";
  .el-button--danger{
    width: 111px;
    height: 40px;
    background: #EA3D5A;
    border-radius: 20px;
    border-color:#EA3D5A;
    font-weight: bold;
    font-size:16px;
    text-align: center;
  }
    *{
      font-size: 16px;
      font-family: "微软雅黑";
      color: rgb(255, 255, 255);
    }
    /*导航栏CSS*/
    .nav-menu {
      border: 0 !important;
      box-shadow: 0px 3px 10px 0px rgba(153, 153, 153, 0.2);
    }
    /*!*居中容器*!*/
    /*.container {*/
      /*width: 1180px;*/
      /*margin: 0 auto;*/
      /*background-color:rgb(31, 41, 55) ;*/
    /*}*/
    .login {
      float: right;
      margin: 10px 20px;
    }
    .item-right {
      float: right;
      font-size: 16px;
      font-family:
        /*STXihei, "华文细黑", "Microsoft YaHei", */
        "微软雅黑";
    }
    .nav-icon {
      float: left;
      height: 60px;
      line-height: 60px;
      margin-left: 30px;
    }
</style>



