<template>
  <div id="app" style="background: #111827;min-width: 1180px">
    <league-nav/>
    <el-main class="container">
    <league-carousel/>
    <league-card/>
    <league-dapps/>
      <el-row :gutter="10">
        <el-tooltip class="item" effect="dark" content="暂时还不能提交应用信息" placement="bottom">
          <el-button class="upload-input" type="danger" @click="justClick()" round>{{$t('button.submit')}}</el-button>
        </el-tooltip>
      </el-row>
    </el-main>
    <league-footer/>
  </div>
</template>

<script>
  import request from '@/utils/request.js'
  import LeagueNav from "@/components/league-nav";
  import LeagueFooter from "@/components/league-footer";
  import LeagueCarousel from "@/components/league-carousel";
  import LeagueCard from "@/components/league-card";
  import LeagueDapps from "@/components/league-dapps";
  export default {
    name: 'App',
    components: {LeagueDapps, LeagueCard, LeagueCarousel, LeagueFooter, LeagueNav},
    data() {
      return {
        dialogVisible:"false",
        //语言环境
        lang: 'cn'
      }
    },
    watch:{
      currentPage(val,oldVal){
        if (this.tagButton !== "All")
        {
          this.dataShow(this.tagDapp);
        }
        else {
          this.dataShow(this.dapps);
        }
      }
    },
    methods: {
      //切换语言
      switchLang()  {
        this.$i18n.locale = this.lang
      },
      justClick(){
        return null;
      },
    },
    mounted() {
      this.switchLang();
    }
  }
</script>

<style lang="scss">
  /*自定义input框*/
  /*.upload-input {*/
    /*.el-input__inner {*/
      /*background: #111827!important;*/
      /*border-color: #1f2937!important;*/
      /*width: 1150px;*/
      /*height: 60px;*/
      /*color: white;*/
      /*box-shadow: 0 0 10px 0px #EA3D5A;*/
    /*}*/
  /*}  */
  .upload-input {
         /*background: #111827!important;*/
         /*border-color: #1f2937!important;*/
         width: 1140px!important;
         height: 60px!important;
         color: white;
         /*box-shadow: 0 0 10px 0px #EA3D5A;*/
     }
  .bottom {
    clear: both;
    text-align: center;
  }
  .item {
    margin: 4px;
  }
</style>
<style lang="scss" scoped>
  /*定义全局字体样式*/
  #app {
    font-size: 16px;
    font-family: "微软雅黑";
    color: rgb(255, 255, 255);

  }
  .el-button--danger{
    width: 111px;
    height: 40px;
    background: #EA3D5A;
    border-radius: 20px;
    border-color:#EA3D5A;
    font-weight: bold;
    font-size:16px;
  }
  /*居中容器*/
  .container {
    width: 1180px;
    margin: 0 auto;
    background-color:#111827 ;
  }
  .upload-dapp{
    height: 90px;
    background: #1F2937;
    box-shadow: 0px 3px 10px 0px rgba(153, 153, 153, 0.2);
    border-radius: 55px;
    padding-top: 40px;
  }

</style>



